import React from "react"
import { Link } from "gatsby"
import moment from "moment"
import styled from "styled-components"
import PostMetaItem from "../components/post-meta-item"

const ContentWrapper = styled.div`
  margin-bottom: 0.5rem;
  padding: 1rem;
  background-color: #f2f2f2;
`

const Title = styled.h3`
  margin: 0 0 0.8rem 0;
  padding: 0;
  font-size: 1rem;
  background-color: #f2f2f2;
  & a {
    color: #000000;
  }
  & a:hover {
    color: #202c6a;
  }
`

const Excerpt = styled.div`
  margin: 0;
  padding: 0;
  font-size: 0.8rem;
`

const PostMetaList = styled.div`
  margin: 0.5rem 0 0 0;
  padding: 0.1rem 0rem;
  text-align: right;
  color: #333;
  font-size: 0.7rem;
  font-weight: light;
`

const PostPreview = ({ post }) => {
  const formattedDate = moment(post.frontmatter.date)
    .local()
    .format(`YYYY/MM/DD`)
  const timeToRead = `${post.timeToRead} min`
  return (
    <ContentWrapper>
      <Title>
        <Link to={post.fields.slug}>{post.frontmatter.title}</Link>{" "}
      </Title>
      <Excerpt>{post.excerpt}</Excerpt>
      <PostMetaList>
        <PostMetaItem icon="ðŸ—“" iconLabel="Date" text={formattedDate} />
        {` Â· `}
        <PostMetaItem icon="â±" iconLabel="timeToRead" text={timeToRead} />
      </PostMetaList>
    </ContentWrapper>
  )
}

export default PostPreview
